'use strict';

import {WinJSDec, fnError, appError} from './APP/config';

const name = 'YX_CALL';
const YX_CALL = (tel) => {
  if (!isNaN(tel)) {
    WinJSDec(_ => {
      window.WinJSBridge.call('tools', 'calltel', {tel});
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default YX_CALL;